import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import CircularProgress from "@material-ui/core/CircularProgress";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import Alert from "@material-ui/lab/Alert";
import Grid from "@material-ui/core/Grid";
import Switch from "@material-ui/core/Switch";
import { AddSizeOfClient } from "../../../DAL/sizeofclient/sizeofclient";
import { useSnackbar } from "notistack";
import { makeStyles } from "@material-ui/core/styles";
import { withRouter } from "react-router-dom";
import { invokeApi } from "../../../bl_libs/invokeApi";
import { color_palette } from "../../../theme/theme";
import {
  Avatar,
  FormControl,
  IconButton,
  InputLabel,
  Select
} from "@material-ui/core";
const useStyles = makeStyles(theme => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: color_palette.primary
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  txt: {
    color: "black"
  },
  alert: {
    width: "100%"
  },
  swtch: {
    marginTop: "20px"
  },
  loading: {
    marginLeft: "50%",
    marginTop: "20%"
  }
}));

export default function AddSizeClient(props) {
    const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const [size_of_client_name, setSizeOfCLientName] = useState("");
  const [status, setStatus] = useState(false);
  const [waiting, setWaiting] = useState(true);
  //---------Functions-------------//
  const handleSizeOfCLientName = event => {
    setSizeOfCLientName(event.target.value);
  };
  const handleSizeOfClientStatus = event => {
    setStatus(!status);
  };
  const handlesubmit = async event => {
    event.preventDefault();
    setWaiting(false);
    const response = await AddSizeOfClient({
    size_of_client_served_name: size_of_client_name,
      status: status
    });
    setWaiting(true);
    if (response.code !== 200) {
      enqueueSnackbar(response.message, { variant: "error" });
    } else {
      props.history.push("/size-of-clients");
    }
  };
  return (
    <>
      {waiting == false ? (
        <CircularProgress className={classes.loading} />
      ) : (
        <Container component="main" maxWidth="sm">
          <CssBaseline />

          <IconButton
            color="primary"
            aria-label="delete"
            style={{ marginLeft: -22 }}
            onClick={() => props.history.goBack()}
          >
            <ArrowBackIcon />
          </IconButton>
          <div className={classes.paper}>
            <Avatar className={classes.avatar}>
              {/* <CollectionsBookmarkIcon /> */}
            </Avatar>
            <Typography className={classes.txt} component="h1" variant="h5">
              Add Size Of CLient
            </Typography>

            <form className={classes.form} onSubmit={handlesubmit}>
              <Grid container spacing={3}>
                <Grid item xs={12}>
                  <TextField
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    id="size_of_client_name"
                    value={size_of_client_name}
                    onChange={handleSizeOfCLientName}
                    label="Name"
                    name="Name"
                    autoComplete="off"
                    autoFocus
                  />
                </Grid>
                <Grid item xs={12}>
                  <FormControl variant="outlined" fullWidth>
                    <InputLabel htmlFor="outlined-age-native-simple">
                      Status
                    </InputLabel>
                    <Select
                      native
                      value={status}
                      onChange={handleSizeOfClientStatus}
                      label="Status"
                      inputProps={{
                        name: "status",
                        id: "status"
                      }}
                    >
                      <option value="true">Active</option>
                      <option value="false">Inactive</option>
                    </Select>
                  </FormControl>
                </Grid>
              </Grid>
              <Button
                type="submit"
                size="large"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
              >
                Add Size Of CLient
              </Button>
            </form>
          </div>
        </Container>
      )}
    </>
  );
}


